<template>
  <section class="p-6 max-w-5xl mx-auto">
    <!-- ÏÉÅÎã® Ïï°ÏÖò ÎùºÏù∏ -->
    <div class="form-action">
      <!-- ‚Üê Î™©Î°ùÏúºÎ°ú -->
      <MaterialButton color="dark" size="sm" variant="outlined" @click="goBack">
        ‚Üê Î™©Î°ùÏúºÎ°ú
      </MaterialButton>

      <!-- Ïò§Î•∏Ï™Ω: ÏÉÅÌÉúÏóê Îî∞Î•∏ Ïï°ÏÖò Î≤ÑÌäº Î¨∂Ïùå -->
      <div class="flex items-center gap-2">
        <!-- ÏûëÏÑ±ÌïòÍ∏∞ (Îã¥ÎãπÏûêÎßå / CD1, CD3) -->
        <MaterialButton
          v-if="role === 2 && (status === 'CD1' || status === 'CD3')"
          color="dark"
          size="sm"
          @click="goWrite"
        >
          ÏûëÏÑ±ÌïòÍ∏∞
        </MaterialButton>

        <!-- ÏàòÏ†ïÌïòÍ∏∞ (Îã¥ÎãπÏûêÎßå / CD4) -->
        <MaterialButton
          v-else-if="role === 2 && status === 'CD4'"
          color="dark"
          size="sm"
          @click="goEdit"
        >
          ÏàòÏ†ïÌïòÍ∏∞
        </MaterialButton>

        <!-- Ïû¨ÏàòÏ†ïÌïòÍ∏∞ (Îã¥ÎãπÏûêÎßå / CD7) -->
        <MaterialButton
          v-else-if="role === 2 && status === 'CD7'"
          color="dark"
          size="sm"
          @click="goEdit"
        >
          Ïû¨ÏàòÏ†ïÌïòÍ∏∞
        </MaterialButton>
      </div>
    </div>

    <!-- Î∞îÍπ• ÏÉÅÏÑ∏ Ïπ¥Îìú -->
    <div class="detail-card">
      <!-- Ìó§Îçî -->
      <header class="flex justify-between items-start detail-header">
        <div>
          <h2 class="text-2xl font-semibold">ÏßÄÏõêÍ≤∞Í≥º ÏÉÅÏÑ∏</h2>
        </div>

        <!-- ÏÉÅÌÉú Î±ÉÏßÄ -->
        <span class="status-pill" :class="statusClass(status)">
          {{ statusLabel(status) }}
        </span>
      </header>

      <!-- Î°úÎî© / ÏóêÎü¨ -->
      <div v-if="loading" class="text-sm text-gray-500 py-6">
        ÏßÄÏõêÍ≤∞Í≥º Ï†ïÎ≥¥Î•º Î∂àÎü¨Ïò§Îäî Ï§ëÏûÖÎãàÎã§...
      </div>
      <div v-else-if="error" class="text-sm text-red-500 py-6">
        {{ error }}
      </div>

      <!-- Î≥∏Î¨∏ -->
      <template v-else>
        <!-- Í∏∞Î≥∏Ï†ïÎ≥¥ Ïπ¥Îìú (Í∑∏Î¶¨Îìú) -->
        <div class="meta-card">
          <div class="meta-grid">
            <!-- 1. ÏßÄÏõêÏûê -->
            <div class="meta-item">
              <span class="meta-label">ÏßÄÏõêÏûê</span>
              <span class="meta-value">
                {{ basicInfo.childName || "Î≥∏Ïù∏" }}
              </span>
            </div>

            <!-- 2. Î≥¥Ìò∏Ïûê -->
            <div class="meta-item">
              <span class="meta-label">Î≥¥Ìò∏Ïûê</span>
              <span class="meta-value">
                {{ basicInfo.guardianName || "-" }}
              </span>
            </div>

            <!-- 3. Îã¥ÎãπÏûê -->
            <div class="meta-item">
              <span class="meta-label">Îã¥ÎãπÏûê</span>
              <span class="meta-value">
                {{ basicInfo.assigneeName || "-" }}
              </span>
            </div>

            <!-- 4. Ïû•Ïï†Ïú†Ìòï -->
            <div class="meta-item">
              <span class="meta-label">Ïû•Ïï†Ïú†Ìòï</span>
              <span class="meta-value">
                {{ basicInfo.disabilityType || "-" }}
              </span>
            </div>

            <!-- Ïö∞ÏÑ†ÏàúÏúÑ -->
            <div class="meta-item">
              <span class="meta-label">Ïö∞ÏÑ†ÏàúÏúÑ</span>
              <span class="meta-value">
                {{ priorityLabel(basicInfo.level) || "-" }}
              </span>
            </div>

            <!-- 5. Í≥ÑÌöçÏûëÏÑ±Ïùº (Í≥ÑÌöçÏÑú ÏûëÏÑ±/Ï†úÏ∂úÏùº) -->
            <div class="meta-item">
              <span class="meta-label">Í≥ÑÌöçÏûëÏÑ±Ïùº</span>
              <span class="meta-value">
                <MaterialButton color="dark" size="sm" @click="openPlanDetail">
                  {{ formattedPlanSubmitAt }}
                </MaterialButton>
              </span>
            </div>

            <!-- 6. Ïã§Ï†ú ÏßÑÌñâÍ∏∞Í∞Ñ -->
            <div class="meta-item">
              <span class="meta-label">Ïã§Ï†ú ÏßÑÌñâÍ∏∞Í∞Ñ</span>
              <span class="meta-value">
                <span>{{ mainForm.actualStart || "ÎØ∏ÏßÄÏ†ï" }}</span>
                <span class="mx-1">~</span>
                <span>{{ mainForm.actualEnd || "ÎØ∏ÏßÄÏ†ï" }}</span>
              </span>
            </div>
          </div>
        </div>

        <!-- CD1 / CD3 : ÏïàÎÇ¥Îßå ÎÖ∏Ï∂ú -->
        <div v-if="isTemp" class="info-card muted-info">
          <template v-if="status === 'CD1'">
            Ïù¥ ÏßÄÏõêÍ≤∞Í≥ºÎäî <strong>ÏûÑÏãú Ï†ÄÏû•</strong> ÏÉÅÌÉúÏûÖÎãàÎã§.<br />
          </template>

          <template v-else>
            Ïù¥ ÏßÄÏõêÍ≤∞Í≥ºÎäî ÏïÑÏßÅ <strong>ÏûëÏÑ± Ï†Ñ</strong> ÏÉÅÌÉúÏûÖÎãàÎã§.<br />
          </template>
        </div>

        <!-- Ïã§Ï†ú ÎÇ¥Ïö© (ÏûÑÏãú/ÏûëÏÑ±Ï†ÑÏù¥ ÏïÑÎãê ÎïåÎßå) -->
        <template v-else>
          <!-- Î©îÏù∏ Í≤∞Í≥º Ïπ¥Îìú -->
          <div class="block-card">
            <h3 class="block-title">Î©îÏù∏ Í≤∞Í≥º</h3>

            <div class="field-block">
              <div class="field-label">Í≥ÑÌöçÌñàÎçò Î™©Ìëú</div>
              <div class="field-value">
                {{ mainForm.goal || "-" }}
              </div>
            </div>

            <div class="field-block">
              <div class="field-label">Í≤∞Í≥º ÎÇ¥Ïö© (ÏùºÎ∞òÏö©)</div>
              <div class="field-value whitespace-pre-line">
                {{ mainForm.publicContent || "-" }}
              </div>
            </div>

            <!-- Í¥ÄÎ¶¨ÏûêÏö© ÎÇ¥Ïö©: role !== 1 Ïùº ÎïåÎßå -->
            <div v-if="role !== 1" class="field-block">
              <div class="field-label">Í≤∞Í≥º ÎÇ¥Ïö© (Í¥ÄÎ¶¨ÏûêÏö©)</div>
              <div class="field-value whitespace-pre-line">
                {{ mainForm.privateContent || "-" }}
              </div>
            </div>

            <!-- Ï≤®Î∂Ä ÌååÏùº: role !== 1 Ïùº ÎïåÎßå -->
            <div v-if="role !== 1" class="field-block mt-3">
              <div class="field-label">Ï≤®Î∂Ä ÌååÏùº</div>
              <div v-if="attachments.length">
                <ul class="mt-1 text-xs text-gray-700 space-y-1">
                  <li
                    v-for="file in attachments"
                    :key="file.attachCode"
                    class="file-row"
                  >
                    <a
                      :href="file.url"
                      target="_blank"
                      rel="noopener noreferrer"
                      class="file-link"
                    >
                      {{ file.originalFilename }}
                    </a>
                  </li>
                </ul>
              </div>
              <div v-else class="text-xs text-gray-400">
                Ï≤®Î∂ÄÎêú ÌååÏùºÏù¥ ÏóÜÏäµÎãàÎã§.
              </div>
            </div>
          </div>

          <!-- Ï∂îÍ∞Ä Í≤∞Í≥º Ïπ¥ÎìúÎì§ -->
          <div class="space-y-4 mt-4">
            <h3 class="font-semibold text-lg">Ï∂îÍ∞Ä Í≤∞Í≥º</h3>

            <template v-if="resultItems.length">
              <div
                v-for="(item, idx) in resultItems"
                :key="item.id || idx"
                class="block-card"
              >
                <div
                  class="flex justify-between items-center text-sm mb-2 border-b border-gray-200 pb-2"
                >
                  <div class="font-medium">Í≤∞Í≥º #{{ idx + 1 }}</div>
                </div>

                <div class="field-block">
                  <div class="field-label">Í≥ÑÌöçÌñàÎçò Î™©Ìëú</div>
                  <div class="field-value">
                    {{ item.goal || "-" }}
                  </div>
                </div>

                <div class="field-block">
                  <div class="field-label">Í≤∞Í≥º ÎÇ¥Ïö© (ÏùºÎ∞òÏö©)</div>
                  <div class="field-value whitespace-pre-line">
                    {{ item.publicContent || "-" }}
                  </div>
                </div>

                <div v-if="role !== 1" class="field-block">
                  <div class="field-label">Í≤∞Í≥º ÎÇ¥Ïö© (Í¥ÄÎ¶¨ÏûêÏö©)</div>
                  <div class="field-value whitespace-pre-line">
                    {{ item.privateContent || "-" }}
                  </div>
                </div>
              </div>
            </template>

            <p v-else class="text-sm text-gray-500">Ï∂îÍ∞Ä Í≤∞Í≥ºÍ∞Ä ÏóÜÏäµÎãàÎã§.</p>
          </div>
        </template>
      </template>
    </div>

    <!-- üî• Í¥ÄÎ¶¨Ïûê(3) Ï†ÑÏö© ÏòÅÏó≠: Î∞òÎ†§ Ïù¥Î†• + ÏäπÏù∏/Î∞òÎ†§ Î≤ÑÌäº -->
    <div
      v-if="role === 3 && (status === 'CD4' || status === 'CD6')"
      class="pt-4 border-t mt-2 space-y-3"
    >
      <!-- ‚õî ÎßàÏßÄÎßâ Î∞òÎ†§ Ïù¥Î†• (ÏûàÏùÑ ÎïåÎßå ÎÖ∏Ï∂ú) -->
      <div v-if="rejectionInfo && rejectionInfo.reason" class="rejection-card">
        <div class="font-semibold mb-1 text-sm">Î∞òÎ†§ Ïù¥Î†•</div>

        <div class="mb-1">
          Î∞òÎ†§ÏùºÏûê:
          <span class="font-medium">
            {{ formattedRejectionDate }}
          </span>
        </div>

        <div>
          <div class="font-medium">ÏÇ¨Ïú†:</div>
          <p class="whitespace-pre-line mt-1">
            {{ rejectionInfo.reason }}
          </p>
        </div>
      </div>

      <!-- ÏäπÏù∏/Î∞òÎ†§ Î≤ÑÌäº -->
      <div class="approve-actions">
        <MaterialButton
          color="dark"
          size="sm"
          class="px-4"
          @click="handleApprove"
        >
          ÏäπÏù∏
        </MaterialButton>
        <MaterialButton
          color="dark"
          size="sm"
          class="px-4"
          @click="handleReject"
        >
          Î∞òÎ†§
        </MaterialButton>
      </div>
    </div>

    <!-- üîª Î∞òÎ†§ ÏÇ¨Ïú† ÏûÖÎ†• Î™®Îã¨ -->
    <div v-if="rejectModalOpen" class="modal-overlay">
      <div class="modal-container">
        <h3 class="text-lg font-semibold mb-3">Î∞òÎ†§ ÏÇ¨Ïú† ÏûÖÎ†•</h3>

        <MaterialTextarea
          id="result-reject-reason"
          variant="outline"
          :rows="4"
          placeholder="Î∞òÎ†§ ÏÇ¨Ïú†Î•º ÏûÖÎ†•ÌïòÏÑ∏Ïöî..."
          :value="rejectReason"
          @input="(e) => (rejectReason = e.target.value)"
        />

        <div class="modal-actions">
          <MaterialButton color="dark" size="sm" @click="closeRejectModal">
            Ï∑®ÏÜå
          </MaterialButton>
          <MaterialButton color="dark" size="sm" @click="confirmReject">
            Î∞òÎ†§
          </MaterialButton>
        </div>
      </div>
    </div>
  </section>
</template>

<script setup>
import { ref, computed, onMounted } from "vue";
import { useRoute, useRouter } from "vue-router";
import axios from "axios";

import MaterialButton from "@/components/MaterialButton.vue";
import MaterialTextarea from "@/components/MaterialTextarea.vue";
import { useAuthStore } from "@/store/authLogin";

const route = useRoute();
const router = useRouter();

const authStore = useAuthStore();
authStore.reload();

// ÎùºÏö∞ÌÑ∞ÏóêÏÑú Î∞õÏùÄ Í∞íÎì§
const resultCode = Number(route.params.resultCode || 0);
const submitCode = Number(route.query.submitCode || 0);
const planCode = Number(route.query.planCode || 0);

// ÏøºÎ¶¨Î°ú ÎÑòÏñ¥Ïò® role (1: ÏùºÎ∞ò, 2: Îã¥ÎãπÏûê, 3: Í¥ÄÎ¶¨Ïûê, 4: ÏãúÏä§ÌÖú)
const role = computed(() => Number(route.query.role || 0));

// ÏÉÅÌÉú Ï†ÄÏû•
const status = ref("");
// CD1(ÏûÑÏãú), CD3(ÏûëÏÑ±Ï†Ñ) Îëò Îã§ isTemp Î°ú Ï≤òÎ¶¨
const isTemp = computed(() => status.value === "CD1" || status.value === "CD3");

// Í∏∞Î≥∏ Ï†ïÎ≥¥
const basicInfo = ref({
  childName: "",
  guardianName: "",
  assigneeName: "",
  disabilityType: "",
  planSubmitAt: "",
  level: "",
});

const formattedPlanSubmitAt = computed(() => {
  const v = basicInfo.value.planSubmitAt;
  return v ? String(v).slice(0, 10) : "-";
});

// Î©îÏù∏ Í≤∞Í≥º Ìèº
const mainForm = ref({
  resultDate: "",
  actualStart: "",
  actualEnd: "",
  goal: "",
  publicContent: "",
  privateContent: "",
});

// Ï∂îÍ∞Ä Í≤∞Í≥º
const resultItems = ref([]);

// Ï≤®Î∂ÄÌååÏùº
const attachments = ref([]);

const loading = ref(false);
const error = ref("");

// üîª Î∞òÎ†§ Î™®Îã¨ ÏÉÅÌÉú
const rejectModalOpen = ref(false);
const rejectReason = ref("");

// üîª ÎßàÏßÄÎßâ Î∞òÎ†§ Ïù¥Î†•
const rejectionInfo = ref({
  reason: "",
  date: "",
});

const formattedRejectionDate = computed(() => {
  const v = rejectionInfo.value?.date;
  return v ? String(v).slice(0, 10) : "-";
});

// Ïò§Îäò YYYY-MM-DD
function getTodayStr() {
  const d = new Date();
  return d.toISOString().slice(0, 10);
}

// Í∏∞Î≥∏ Ï†ïÎ≥¥ Î∂àÎü¨Ïò§Í∏∞ (Í≤∞Í≥ºÏö© API)
async function loadBasicInfo() {
  if (!submitCode) return;

  const { data } = await axios.get(`/api/result/${submitCode}`);

  if (!data?.success || !data.result) {
    throw new Error(data?.message || "ÏßÄÏõêÍ≤∞Í≥º Í∏∞Î≥∏ Ï†ïÎ≥¥Î•º Ï∞æÏùÑ Ïàò ÏóÜÏäµÎãàÎã§.");
  }

  const res = data.result;

  basicInfo.value = {
    childName: res.childName || "",
    guardianName: res.guardianName || "",
    assigneeName: res.assigneeName || "-",
    disabilityType: res.disabilityType || "-",
    planSubmitAt: res.planSubmitAt || "",
    level: res.level || "",
  };
}

// Í≤∞Í≥º ÏÉÅÏÑ∏ Î∂àÎü¨Ïò§Í∏∞
async function loadDetail() {
  const { data } = await axios.get(`/api/result/detail/${resultCode}`);

  if (!data?.success || !data.result) {
    throw new Error(data?.message || "ÏßÄÏõêÍ≤∞Í≥º Ï†ïÎ≥¥Î•º Ï∞æÏùÑ Ïàò ÏóÜÏäµÎãàÎã§.");
  }

  const d = data.result;

  status.value = d.status || "";

  mainForm.value = {
    resultDate: d.main?.resultDate
      ? String(d.main.resultDate).slice(0, 10)
      : getTodayStr(),
    actualStart: d.main?.actualStart || "",
    actualEnd: d.main?.actualEnd || "",
    goal: d.main?.goal || "",
    publicContent: d.main?.publicContent || "",
    privateContent: d.main?.privateContent || "",
  };

  resultItems.value =
    (d.items || []).map((it, idx) => ({
      id: it.resultItemCode || Date.now() + idx,
      goal: it.goal || "",
      publicContent: it.publicContent || "",
      privateContent: it.privateContent || "",
    })) || [];

  attachments.value =
    (d.attachments || []).map((a) => ({
      attachCode: a.attachCode,
      originalFilename: a.originalFilename,
      url: a.url,
    })) || [];
}

// üîπ ÎßàÏßÄÎßâ Î∞òÎ†§ Ïù¥Î†• Ï°∞Ìöå (Í¥ÄÎ¶¨ÏûêÏö© ÌëúÏãú)
async function loadRejectionInfo() {
  try {
    const { data } = await axios.get(
      `/api/result/${resultCode}/rejection-reason`
    );

    if (data?.success && data.result) {
      const r = data.result;
      rejectionInfo.value = {
        reason: r.rejection_reason || "",
        date: r.rejection_date || r.approval_date || "",
      };
    } else {
      rejectionInfo.value = { reason: "", date: "" };
    }
  } catch (e) {
    console.error("[loadRejectionInfo]", e);
    rejectionInfo.value = { reason: "", date: "" };
  }
}

function priorityLabel(code) {
  const c = (code || "").toUpperCase();
  switch (c) {
    case "BB1":
      return "Í∏¥Í∏â";
    case "BB2":
      return "Ï§ëÏ†ê";
    case "BB3":
      return "Í≥ÑÌöç";
    default:
      return "-";
  }
}

onMounted(async () => {
  try {
    loading.value = true;

    if (!resultCode) {
      throw new Error("resultCodeÍ∞Ä ÏóÜÏäµÎãàÎã§. (ÎùºÏö∞ÌÑ∞ params ÌôïÏù∏ ÌïÑÏöî)");
    }

    // 1) ÏÉÅÏÑ∏ Î®ºÏ†Ä Î°úÎìú
    await loadDetail();

    // 2) Í∏∞Î≥∏Ï†ïÎ≥¥Îäî submitCode ÏûàÏùÑ ÎïåÎßå
    if (submitCode) {
      await loadBasicInfo();
    }

    // 3) Í¥ÄÎ¶¨ÏûêÎùºÎ©¥ Î∞òÎ†§ Ïù¥Î†•ÎèÑ Í∞ôÏù¥ Ï°∞Ìöå
    if (role.value === 3) {
      await loadRejectionInfo();
    }
  } catch (e) {
    console.error(e);
    error.value = e.message || "ÏßÄÏõêÍ≤∞Í≥º Ï°∞Ìöå Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.";
  } finally {
    loading.value = false;
  }
});

/* ---------- ÏÉÅÌÉú ÎùºÎ≤® / Ïä§ÌÉÄÏùº ---------- */
function statusLabel(code) {
  const c = (code || "").toString().toUpperCase();
  switch (c) {
    case "CD1":
      return "ÏûÑÏãúÏ†ÄÏû•";
    case "CD3":
      return "ÏûëÏÑ±Ï†Ñ";
    case "CD4":
      return "Í≤ÄÌÜ†Ï†Ñ";
    case "CD5":
      return "Í≤ÄÌÜ†ÏôÑÎ£å";
    case "CD6":
      return "Ïû¨ÏäπÏù∏ÏöîÏ≤≠";
    case "CD7":
      return "Î∞òÎ†§";
    default:
      return code || "-";
  }
}

function statusClass(code) {
  const c = (code || "").toString().toUpperCase();
  switch (c) {
    case "CD1":
    case "CD3":
      return "p-gray";
    case "CD4":
      return "p-yellow";
    case "CD5":
      return "p-green";
    case "CD6":
      return "p-orange";
    case "CD7":
      return "p-red";
    default:
      return "p-gray";
  }
}

// Í≥ÑÌöç ÏÉÅÏÑ∏Î°ú Ïù¥Îèô (PlanDetail ÎùºÏö∞Ìä∏Ïóê ÎßûÍ≤å Í≤ΩÎ°ú ÌïÑÏöîÏãú ÏàòÏ†ï)
function openPlanDetail() {
  if (!planCode) return;
  const roleParam = role.value || "";
  const url = `/plans/detail/${planCode}?submitCode=${submitCode}${
    roleParam ? `&role=${roleParam}` : ""
  }`;

  window.open(url, "_blank");
}

// Î™©Î°ùÏúºÎ°ú
function goBack() {
  router.back();
}

// ÏàòÏ†ï ÌôîÎ©¥ÏúºÎ°ú Ïù¥Îèô
function goEdit() {
  if (!resultCode) return;

  router.push({
    name: "result-edit",
    params: { resultCode },
    query: submitCode ? { submitCode, role: role.value } : { role: role.value },
  });
}

// ÏûëÏÑ± ÌôîÎ©¥ÏúºÎ°ú Ïù¥Îèô
function goWrite() {
  if (!submitCode) return;

  router.push({
    name: "result-write",
    params: { submitcode: submitCode },
    query: { role: role.value },
  });
}

// ‚úÖ ÏäπÏù∏
async function handleApprove() {
  try {
    const processorCode = authStore.userCode; // üîπ Î°úÍ∑∏Ïù∏Ìïú Í¥ÄÎ¶¨Ïûê userCode

    if (!processorCode) {
      alert(
        "Î°úÍ∑∏Ïù∏ Ï†ïÎ≥¥Í∞Ä ÏóÜÏñ¥ ÏäπÏù∏ÏûêÎ•º Í∏∞Î°ùÌï† Ïàò ÏóÜÏäµÎãàÎã§. Îã§Ïãú Î°úÍ∑∏Ïù∏Ìï¥Ï£ºÏÑ∏Ïöî."
      );
      return;
    }

    const { data } = await axios.post(`/api/result/${resultCode}/approve`, {
      processorCode, // üîπ ÏÑúÎ≤ÑÎ°ú Í∞ôÏù¥ Ï†ÑÎã¨
    });

    if (data?.success) {
      alert("ÏßÄÏõêÍ≤∞Í≥ºÍ∞Ä ÏäπÏù∏ÎêòÏóàÏäµÎãàÎã§.");
      await loadDetail();
    } else {
      alert(data.message || "ÏäπÏù∏ Ï≤òÎ¶¨ Ïã§Ìå®");
    }
  } catch (e) {
    console.error(e);
    alert("ÏÑúÎ≤Ñ Ïò§Î•ò: " + (e.response?.data?.message || e.message));
  }
}

// ‚úÖ Î∞òÎ†§ Î≤ÑÌäº ÌÅ¥Î¶≠ ‚Üí Î™®Îã¨ Ïó¥Í∏∞
function handleReject() {
  rejectReason.value = "";
  rejectModalOpen.value = true;
}

// ‚úÖ Î™®Îã¨ ÏïàÏóêÏÑú 'Î∞òÎ†§' ÌôïÏ†ï
async function confirmReject() {
  if (!rejectReason.value.trim()) {
    alert("Î∞òÎ†§ ÏÇ¨Ïú†Î•º ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.");
    return;
  }

  try {
    const { data } = await axios.post(`/api/result/${resultCode}/reject`, {
      reason: rejectReason.value,
    });
    if (data?.success) {
      alert("ÏßÄÏõêÍ≤∞Í≥ºÍ∞Ä Î∞òÎ†§ÎêòÏóàÏäµÎãàÎã§.");
      rejectModalOpen.value = false;
      await loadDetail();
    } else {
      alert(data.message || "Î∞òÎ†§ Ï≤òÎ¶¨ Ïã§Ìå®");
    }
  } catch (e) {
    console.error(e);
    alert("ÏÑúÎ≤Ñ Ïò§Î•ò: " + (e.response?.data?.message || e.message));
  }
}

function closeRejectModal() {
  rejectModalOpen.value = false;
}
